import React, { useState } from 'react';
import { Form, Formik } from 'formik';
import LeftInput from './leftInput';
import RightInput from './rightInput';
import { ClassRegistrationSchema } from './validation';
import Alerts from '../../shared/alerts';

type SemesterProps = '1st Semester' | '2nd Semester' | 'choose';
type termProps = '1st Term' | '2nd Term' | 'Semestral' | 'choose';
interface ClassProps {
  classRoom: string;
  idNumber: string;
  subject: string;
  teacher: string;
  year: string;
}

const initialValues: ClassProps = {
  classRoom: '',
  teacher: '',
  idNumber: '',
  subject: '',
  year: '',
};

const FormClassRegistration = () => {
  const [isSuccess, setIsSuccess] = useState<boolean>(false);
  const [isError, setIsError] = useState('');
  const [schedule, setSchedule] = useState('choose');
  const [semester, setSemester] = useState<SemesterProps>('choose');
  const [term, setTerm] = useState<termProps>('choose');
  const [subjectTime, setSubjectTime] = useState('choose');

  const handleReset = () => {
    setSchedule('choose');
    setSemester('choose');
    setTerm('choose');
    setSubjectTime('choose');
  };
  return (
    <Formik
      initialValues={initialValues}
      validationSchema={ClassRegistrationSchema}
      onSubmit={(values, { setSubmitting, resetForm }) => {
        if (
          schedule === 'choose' ||
          semester === 'choose' ||
          term === 'choose' ||
          subjectTime === 'choose'
        ) {
          setSubmitting(false);
          setIsError('Please Fill up Completely');
        } else {
          let options = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              classRoom: values.classRoom,
              teacher: values.teacher,
              idNumber: values.idNumber,
              subject: values.subject,
              subjectTime,
              semester,
              term,
              schedule,
              year: values.year,
            }),
          };

          fetch(
            'https://classroom-attendance-server-1.herokuapp.com/api/class/registration',
            options
          )
            .then((res) => res.json())
            .then((data) => {
              if (data.success) {
                setSubmitting(false);
                setIsSuccess(true);
                resetForm();
                setSchedule('choose');
                setSemester('choose');
                setTerm('choose');
                setSubjectTime('choose');
              } else {
                setSubmitting(false);
                setIsError('Registration Not Successfull');
              }
            })
            .catch((err) => {
              setSubmitting(false);
              setIsError('Registration Not Successfull');
              console.log('reg err : ', err);
            });
        }

        setTimeout(() => {
          setIsSuccess(false);
          setIsError('');
        }, 7000);
      }}
    >
      {(formik) => (
        <Form className="w-full">
          {isSuccess && (
            <Alerts top="10" success text="Registered Successfully" />
          )}
          {isError && <Alerts top="10" _error text={isError} />}
          <div className="flex flex-row">
            <LeftInput
              semester={semester}
              setSemester={setSemester}
              term={term}
              setTerm={setTerm}
            />
            <RightInput
              formik={formik}
              subjectTime={subjectTime}
              setSubjectTime={setSubjectTime}
              schedule={schedule}
              setSchedule={setSchedule}
              handleReset={handleReset}
            />
          </div>
        </Form>
      )}
    </Formik>
  );
};

export default FormClassRegistration;
