/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import shortid from 'shortid';
import { formatDate } from '../../../lib/formatDate';

interface Props {
  data: any[] | null;
}

const ContainerEnded: React.FC<Props> = ({ data }) => {
  return (
    <div className="flex flex-col items-center w-1/2 bg-red-500 rounded-lg p-5">
      <h1 className="text-red-100 text-2xl font-bold mb-5">
        Ended Classess...
      </h1>
      <h2 className="text-red-100 text-sm font-bold mb-5">
        Today : {formatDate(new Date())}
      </h2>
      {data?.map((record: any[]) => (
        <div
          key={shortid.generate()}
          className="bg-red-200 p-3 mb-5 rounded-sm flex flex-row justify-evenly w-5/6"
        >
          <div>
            <p>
              Room :{' '}
              <span className="text-lg font-semibold ml-6"> {record[1]}</span>
            </p>
            <p>
              Teacher :{' '}
              <span className="text-lg font-semibold ml-3"> {record[4]}</span>
            </p>
          </div>
          <div>
            <p>
              Subject :
              <span className="text-lg font-semibold ml-12"> {record[2]}</span>
            </p>
            <p>
              Subject Time :
              <span className="text-lg font-semibold ml-3"> {record[3]}</span>
            </p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default ContainerEnded;
